
.dialog-drag {
  z-index: 101;
  width: auto;
  position: absolute;
  border: #1aad8d solid 2px;
  background-color: #fff;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-name: dialog-anim;
          animation-name: dialog-anim;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}
.dialog-drag .dialog-header {
  position: relative;
  text-align: left;
  padding: 0.25em 3em 0.25em 1em;
  width: auto;
  font-size: 0.9em;
  background-color: #1aad8d;
  color: #fff;
}
.dialog-drag .dialog-header button.close {
  position: absolute;
  right: 0;
  top: 0;
  margin: 0.25em 0.25em 0 0;
  background: transparent;
  box-shadow: none;
  border: none;
  color: #fff;
  z-index: 105;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5), 1px 1px 1px rgba(0,0,0,0.5);
}
.dialog-drag .dialog-header button.close:after {
  content: 'âœ–';
}
.dialog-drag .dialog-header button.close:hover {
  color: #e59e0d;
}
.dialog-drag .dialog-body {
  padding: 1em;
}
.dialog-drag.fixed {
  border-color: #e59e0d;
}
@-webkit-keyframes dialog-anim {
0% {
    opacity: 0;
    -webkit-transform: scaleX(0.1);
            transform: scaleX(0.1);
}
50% {
    -webkit-transform: rotate(1deg);
            transform: rotate(1deg);
}
100% {
    opacity: 1;
}
}
@keyframes dialog-anim {
0% {
    opacity: 0;
    -webkit-transform: scaleX(0.1);
            transform: scaleX(0.1);
}
50% {
    -webkit-transform: rotate(1deg);
            transform: rotate(1deg);
}
100% {
    opacity: 1;
}
}


